import React, { useState, useEffect, useRef } from 'react';
import Card from './Card';
import data from './data/words-and-expressions';
// import data from './data/top-3600';
// import data from './data/sentences-1100';
// import data from './data/sentences';
// import data from './data/nouns--top-2000';
// import data from './data/verbs--all';
import './App.css';
import Divider from '@material-ui/core/Divider';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import VisibilityIcon from '@material-ui/icons/Visibility';
import BackIcon from '@material-ui/icons/NavigateBefore';
import NextIcon from '@material-ui/icons/NavigateNext';
import AddIcon from '@material-ui/icons/Add';
import { BottomNavigation, BottomNavigationAction } from '@material-ui/core';
import TopN from './TopN';
import ArrowKeysReact from 'arrow-keys-react';
import SwipeableViews from 'react-swipeable-views';
import decks from './data/decks';
// const español = ['cantar', 'hablar', 'haber', 'vivir', 'adivinar', 'correr', 'andar'];
// const english = ['to sing', 'to speak', 'to have', 'to live', 'to guess', 'to run', 'to walk'];

function getRandomInt(max) {
  return Math.floor(Math.random() * Math.floor(max));
}

const history = [];

function App() {

  const appRef = useRef();
  useEffect(() => appRef.current.focus());

  ArrowKeysReact.config({
    left: handleBack,
    right: handleNext,
    down: () => setShowAnswer(true),
  });

  const [wordIndex, setWordIndex] = useState(0);
  const [reverse, toggleReverse] = useState(false);
  const [animate, setAnimate] = useState(true);
  const [back, setBack] = useState(false);
  const [showAnswer, setShowAnswer] = useState(false);
  const [deck, setDeck] = useState(data);

  function handleNext() {
    const _wordIndex = getRandomInt(deck.length - 1);
    history.push(_wordIndex);
    setBack(false);
    setAnimate(true);
    setShowAnswer(false);
    setWordIndex(_wordIndex);
  }

  function handleBack() {
    history.pop();
    setBack(true);
    setAnimate(true);
    setShowAnswer(false);
    setWordIndex(history[history.length - 1] || 0);
  }

  function handleReverse() {
    setAnimate(false);
    toggleReverse(!reverse);
  }

  async function loadDeck(fileName) {
    const module = await import(`./data/${fileName}`);
    setDeck(module.default);
  }

  let question, answer;
  if (!reverse)
    [question, answer] = deck[wordIndex];
  else
    [answer, question] = deck[wordIndex];

  console.log(question, answer);
  return <SwipeableViews axis='x'>
    <div className="App" ref={appRef} {...ArrowKeysReact.events} tabIndex='1'>
      <Card
        key={question}
        question={question}
        answer={answer}
        showAnswer={showAnswer}
        onNext={handleNext}
        onBack={handleBack}
        onReverse={handleReverse}
        reverse={reverse}
        history={back}
        animate={animate}
      />
      <TopN show={wordIndex < 100}>Top 100</TopN>
      <BottomNavigation style={{ position: 'fixed', bottom: 0, width: '100%' }} >
        <BottomNavigationAction label="Back" onClick={handleBack} icon={<BackIcon />} />
        <BottomNavigationAction label="Show Answer" onClick={() => setShowAnswer(!showAnswer)} icon={<VisibilityIcon />} />
        <BottomNavigationAction label="Next" onClick={handleNext} icon={<NextIcon />} />
      </BottomNavigation>
    </div>
    <>
      <div style={{ width: '100vw', height: '100vh', backgroundColor: '#EEE', overflow: 'hidden', padding: '48px' }}>
        <h2>Choose a Deck...</h2>
        <List> {decks.map(deck =>
          <ListItem>
            <ListItemText primary={deck.name} onClick={() => loadDeck(deck.fileName)} />
          </ListItem>
        )}
        </List>
      </div>
      <BottomNavigation style={{ position: 'fixed', bottom: 0, width: '100%' }} >
        <BottomNavigationAction label="Next" onClick={loadDeck} icon={<AddIcon />} />
      </BottomNavigation>
    </>
  </SwipeableViews>
}

export default App;
