function Deck(name, fileName) {
    return ({ name, fileName });
}

const decks = [
    new Deck('Verbs', 'verbs--all'),
    new Deck('Top 2000 Nouns', 'nouns--top-2000'),
    new Deck('Sentences', 'sentences'),
    new Deck('More Sentences', 'sentences-1100'),
    new Deck('No Verbs', 'top-3600'),
    new Deck('Words and Expressions', 'words-and-expressions')
]

export default decks;